<template>
  <div class="my">
    <div class="login" v-if="isLogin">
      <van-nav-bar title="我的" :border="false" left-text="返回" left-arrow @click-left="onClickLeft" />

      <div class="my-box">
        <div class="my-msg" :style="{backgroundImage:'url('+ userInfo.userBg +')'}">
          <div class="msg-box">
            <van-uploader class="v-uploader" :after-read="afterRead" />
            <div class="msg-auatar" :style="{backgroundImage:'url('+ userInfo.userImg +')'}"></div>
            <div class="msg-content">
              <p>{{userInfo.nickName}}</p>
              <div>{{userInfo.desc == '' ? '这个人很懒，什么都没留下':userInfo.desc}}</div>
            </div>
          </div>
        </div>
        <div class="my-administration">
          <div
            @click="toPage(item)"
            class="my-item"
            v-for="(item, index) in administration"
            :key="index"
          >
            <div class="icon iconfont" :class="item.icon"></div>
            <div class="title">{{item.text}}</div>
          </div>
        </div>
      </div>

      <div class="main-box">
        <div
          class="main-item border-bottom"
          @click="toPage(item)"
          v-for="(item,index) in actionsArr"
          :key="index"
        >
          <div class="main-item-left iconfont" :class="item.icon"></div>
          <div class="main-item-mid">{{item.text}}</div>
          <div class="main-item-right iconfont icon-zhankai"></div>
        </div>
      </div>
    </div>
    <div class="not-login" v-else>
      <van-nav-bar title="我的" :border="false" left-text="返回" left-arrow @click-left="onClickLeft" />
      <!-- 你还没有登录！ -->
      <div class="notLogin">
        <div class="notLogin-img">
          <div class="tig">你还没有登录呢！</div>
          <div class="goLogin">
            <van-button @click="toLogin" color="#7f5839" type="default">去登录</van-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import Api from "../api/api";
import Util from "../util/utils";
export default {
  computed: {
    ...mapState(["isLogin"]),
    ...mapState("myModule", ["administration", "actionsArr", "userInfo"])
  },
  created() {
    // 是否登录，没有登录拦截
    // 拦截一下
    if (!this.isLogin) {
      // 没有登录的不用查询了
      return;
    }

    // 发起我的请求，看看回来的是什么鬼
    let tokenString = Util.getCookies(this);
    Util.showLoading(this);
    Api.findMy({ tokenString }).then(res => {
      if (res.data.code == "A001") {
        this.$toast(res.data.msg);
        // 初始化数据
        this.changeData({ key: "userInfo", value: res.data.result[0] });
      }
    });
  },
  methods: {
    ...mapMutations("myModule", ["changeData"]),
    onClickLeft() {
      this.$router.go(-1);
    },
    toLogin() {
      this.$router.push({ name: "Login" });
    },
    toPage({ name }) {
      this.$router.push({ name });
    },
    afterRead(file) {
      const fileContent = file.file;
      // 拦截一下 300kb 以上拦截
      const size = 300 * 1024;

      if (fileContent.szie > size) {
        this.$toast("图片上传不能超过300kb");
      }
      Util.showLoading(this, "正在上传");
      const tokenString = Util.getCookies(this);
      const imgType = fileContent.type.split("/")[1];
      const serverBase64Img = file.content.replace(
        `data:image/${imgType};base64,`,
        ""
      );
      Api.updateUserBg({ tokenString, imgType, serverBase64Img }).then(res => {
        if (res.data.code == "I001") {
          this.$toast(res.data.msg);
          // 改变前台图片
          this.userInfo.userBg = res.data.userBg;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/util.scss";
// 把导航栏样式重置

.login .van-nav-bar {
  background-color: transparent;
  position: fixed;
  width: 100%;
  height: 46px;
  top: 0;
  left: 0;

  /deep/ .van-nav-bar__arrow {
    color: #fff;
  }

  /deep/ .van-nav-bar__text {
    color: #fff;
  }
  /deep/ .van-nav-bar__title {
    color: #fff;
  }
}

/deep/ .not-login {
  .van-nav-bar__left {
    .van-nav-bar__arrow,
    .van-nav-bar__text {
      color: #7f5839;
    }
  }
}

// 上面的盒子
.my-box {
  height: 286px;
  position: relative;
  .my-msg {
    padding-top: 46px;
    height: 180px;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .my-administration {
    position: absolute;
    width: 340px;
    left: 50%;
    transform: translateX(-50%);
    height: 80px;
    bottom: 20px;
    z-index: 1;
    border: 1px solid #eee;
    border-radius: 10px;
    background-color: #fff;
  }
}

// 我的管理盒子
.my-administration {
  display: flex;
  padding: 10px 5px;
  width: 100%;
  box-sizing: border-box;
  .my-item {
    width: 25%;
    text-align: center;
    .icon {
      font-size: 32px;
      height: 35px;
      line-height: 35px;
    }
    .title {
      font-size: 18px;
      height: 25px;
      line-height: 25px;
    }
  }
}

// 我的中间盒子
.main-box {
  .main-item {
    display: flex;
    padding: 10px 10px;
    height: 50px;
    line-height: 50px;
    // border-bottom: 1px solid #999;

    .main-item-left,
    .main-item-right {
      font-size: 30px;
      text-align: center;
      width: 60px;
    }
    .main-item-mid {
      padding-left: 30px;
      font-size: 20px;
      width: 235px;
    }
  }
}

// 我的信息盒子
.my-msg {
  .msg-box {
    padding-top: 20px;
    margin: auto;
    width: 340px;
    height: 100px;
    display: flex;
    position: relative;
    .v-uploader {
      width: 40px;
      height: 40px;
      position: absolute;
      right: 0;
      top: 0;

      /deep/ .van-uploader__upload {
        background-color: transparent;
        width: 100%;
        height: 40px;
        margin: 0;
      }
    }
    .msg-auatar {
      padding: 50px;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
      border-radius: 50%;
    }
    .msg-content {
      width: 220px;
      padding-left: 20px;
      color: #fff;
      p {
        height: 30px;
        line-height: 30px;
        font-size: 20px;
        @include ellipseOne();
      }
      div {
        height: 70px;
        line-height: 35px;
        font-size: 16px;
        @include ellipse(2);
      }
    }
  }
}
.notLogin {
  height: 500px;
  position: relative;
  .notLogin-img {
    width: 300px;
    height: 350px;
    background: url("../assets/img/notLogin.jpg") no-repeat;
    background-position: center center;
    background-size: cover;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .goLogin {
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
    .tig {
      font-size: 20px;
      height: 60px;
      padding: 5px;
      text-align: center;
      line-height: 60px;
      position: absolute;
      border: 1px solid #ddd;
      right: 0;
      top: 10px;
      border-radius: 20px;
      &::after {
        content: "";
        position: absolute;
        left: 20px;
        bottom: -40px;
        border-left: 10px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 20px solid transparent;

        border-top: 20px solid #ddd;
      }
    }
  }
}
</style>